import Vue from 'vue'
import Router from 'vue-router'
import Chatting from 'components/chatting/chatting'
import Signin from 'components/signin/signin'
import Manage from 'components/manage/manage'
import axios from 'axios';
Vue.use(Router)

const router=new Router({
   mode: 'history',
   base:"/chat",
  routes: [
    {
      path: '/',
      name:"signin",
      redirect: '/signin'
    },
    {
      path: '/signin',
      name:"signin",
      component: Signin,
      meta:{
      	title:"登录"
      }
    },
    {
      path: '/chatting',
      name:"chatting",
      component:Chatting,
      meta:{
      	title:"聊天室"
      }
    },
    {
      path: '/manage',
      name:"manage",
      component:Manage,
      meta:{
      	title:"后台管理"
      }
      
    }
    
  ]

})


