<template>
  <div id="app">
  	
 
  	 <router-view/>
    
  </div>
</template>

<script>
	import axios from 'axios'
	export default{
		
	created(){
			axios.post("chat/getUserInfo").then(res=>{
				       
				       console.log("app页"+res)
				       
	      		    if(res.data.code==3){
	      		    	this.$router.push('/');
	      		    }else if(res.data.code==1){
	      		    	 this.$router.push({
								      path:'/manage'
							    })		
	      		    }else if(res.data.code==0){
	      		    	 this.$router.push({
								     path:'/chatting'
							     })		
	      		    	 
	      		    }
	      		})
			  
		}
	}
</script>


